export function setupHighlight() {
	// Don't highlight code block's inside editor's content
	// (https://github.com/cksource/umberto/issues/838).
	const blocks = Array.from( document.querySelectorAll( 'pre code' ) )
		.filter( element => !element.closest( '.ck-content' ) );
	const worker = new Worker( window.umberto.relativeAppPath + '/highlight.worker.js' );

	worker.addEventListener( 'message', evt => {
		const data = JSON.parse( evt.data );
		const block = blocks[ data.index ];
		block.innerHTML = data.payload;
		block.classList.add( 'hljs' );
	} );

	blocks.forEach( ( block, index ) => {
		worker.postMessage( JSON.stringify( {
			index,
			payload: block.textContent,
			language: block.className
		} ) );
	} );
}
